import contentModule from '../components/content-module';
import joinOurNewsletter from '../components/join-our-newsletter';
import pageNav from '../components/page-nav';
import latestNewsBanner from '../components/latest-news-banner';
import promoBanner from '../components/promo-banner';
import gallery from '../components/gallery';
import bannerPromo from '../components/banner-promo';
import textBlock from '../components/text-block';
import relatedArtciels from '../components/related-artciels';
import testimonialBlock from '../components/testimonial-block';
import getSocial from '../components/get-social';
import viewVideosOnScroll from '../components/full-screen';
import initPopup from '../components/init-popup';
import venueDirection from '../components/venue-direction';

const eventChild = () => {
	contentModule();
	joinOurNewsletter();
	pageNav();
	latestNewsBanner();
	promoBanner();
	gallery();
	viewVideosOnScroll();
	bannerPromo();
	textBlock();
	relatedArtciels();
	testimonialBlock();
	getSocial();
	initPopup('.js-popup-download-btn', '.js-popup-download');
	venueDirection();
};

export default eventChild;
